<template>
  <div class="productlist">
    <div class="top">
      <el-row :gutter="20">
        <el-col :span="16">
          <div class="grid-content bg-purple" style="padding-bottom: 15px; border-bottom: 1px solid #f7f7f7;">
            <el-select size="small" v-model="modelShop" placeholder="" style="width: 240px;">
              <el-option
                v-for="item in shopList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <el-select size="small" v-model="modelPrice" placeholder="" style="width: 240px;">
              <el-option
                v-for="item in priceList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <a href="javascript:;" class="addproduct" @click="centerDialogVisible = true"><i></i></a>
          </div>
        </el-col>
        <el-col :span="8">
          <div class="grid-content bg-purple" style="float: right; margin-right: 10px;">
            <el-input size="small" v-model="valueSeach" placeholder="" style="width: 240px;"></el-input>
            <el-button>  </el-button>
          </div>
        </el-col>
      </el-row>
      <div class="listWrap">
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column
          prop="id"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="asin"
          label="ASIN"
          width="180">
        </el-table-column>
        <el-table-column
          prop="title"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <el-button plain>  </el-button>
            <el-button type="primary"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
      <el-row>
        <p style="padding-top: 15px;">
          <el-button type="primary" plain></el-button>
        </p>
      </el-row>
    </div>
    <div class="contain">
      <div class="containItem" v-for="item in containItems">
        <div class="productImgwrap">
          <img :src="item.img" alt="" width="100%" height="100%">
          <el-checkbox v-model="checked"></el-checkbox>
        </div>
        <p class="productDetail">
          {{item.productDetail}}
        </p>
        <p class="productPrice">
          <b class="price">{{item.price}}</b>
          <span class="wish"></span>
        </p>

      </div>
    </div>
    <el-dialog
      title=""
      :visible.sync="centerDialogVisible"
      width="46%"
      center
      class="productstyle">
      <p><span>:</span>
        <el-select size="small" v-model="webSiteCode" placeholder="" style="width: 240px;"
                   @change="findwebstore(webSiteCode)">
          <el-option
            v-for="item in webSiteCodeList"
            :key="item.code"
            :label="item.name"
            :value="item.code">
          </el-option>
        </el-select>
      </p>
      <p><span>:</span>
        <el-input size="small" v-model="listing.url" placeholder="" style="width: 74%; margin: 0 10px">
        </el-input>
        <el-button @click="grabproduct()"></el-button>
      </p>
      <div class="dialogmainwrap">
        <div class="leftwrap">
          <p><span class="titledai">:</span>
            <el-input size="small" v-model="listing.title" placeholder=""
                      style="width: 80%; margin-right: 10px"></el-input>
          </p>
          <p><span class="titledai">ID:</span>
            <el-input size="small" v-model="listing.asin" placeholder="ID"
                      style="width: 80%; margin-right: 10px"></el-input>
          </p>
          <p><span class="titledai">:</span>
            <el-input size="small" v-model="listing.webStoreName" placeholder=""
                      style="width: 80%; margin-right: 10px"></el-input>
          </p>
          <p><span class="titledai">ID:</span>
            <el-input size="small" v-model="listing.webStoreSysId" placeholder="ID"
                      style="width: 80%; margin-right: 10px"></el-input>
          </p>
          <p><span class="titledai">:</span>
            <el-input size="small" v-model="listing.brandName" placeholder=""
                      style="width: 80%; margin-right: 10px"></el-input>
          </p>
          <p><span class="titledai">:</span>
            <el-input size="small" v-model="listing.price" placeholder=""
                      style="width: 80%; margin-right: 10px"></el-input>
          </p>
        </div>
        <div class="rightwrap">
          <div class="Qimgwrap">
            <img src="./111.png" alt="" width="100%" height="100%">
          </div>
          <p>
            <el-checkbox v-model="listing.platformDelivery">FBA</el-checkbox>
            <el-checkbox v-model="listing.followSell"></el-checkbox>
          </p>
        </div>
      </div>
      <div class="tagwrap">
        <span class="titledai">:</span>
        <el-tag
          :key="tag"
          v-for="tag in ColordynamicTags"
          closable
          :disable-transitions="false"
          @close="handleClose(tag)">
          {{tag}}
        </el-tag>
        <el-input
          class="input-new-tag"
          v-if="inputVisible"
          v-model="inputValue"
          ref="saveTagInput"
          size="small"
          @keyup.enter.native="handleInputConfirm"
          @blur="handleInputConfirm"
          style="width: 80px;"
        >
        </el-input>
        <!--<el-button v-else class="button-new-tag" size="small" @click="showInput"></el-button>-->
      </div>
      <!-- <div class="tagwrap">
          <span class="titledai">:</span>
           <el-tag
          :key="tag"
          v-for="tag in SizedynamicTags"
          closable
          :disable-transitions="false"
          @close="handleClose(tag)">
          {{tag}}
          </el-tag>
          <el-input
          class="input-new-tag"
          v-if="inputVisible1"
          v-model="inputValue1"
          ref="saveTagInput1"
          size="small"
          @keyup.enter.native="handleInputConfirm"
          @blur="handleInputConfirm"
          style="width: 80px;"
          >
          </el-input>
          <el-button v-else class="button-new-tag" size="small" @click="showInput1"></el-button>
      </div> -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addproduct()"> </el-button>
    </span>
    </el-dialog>
  </div>
</template>
<script>
  import {callApiWithToken} from '@/data/callApi'

  export default {
    props: {},
    data() {
      return {
        tableData:[],
        webSiteCode: '',
        webStoreId: '',
        webSiteCodeList: [],
        webStoreList: [],
        listing: {
          webSiteCode: '',
          webStoreId: '',
          url: '',
          titel: '',
          asin: '',
          webStoreName: '',
          brandName: '',
          webStoreSysId: '',
          price: '',
          platformDelivery: false,
          followSell: false,
          listingProps: ''
        },
        ColordynamicTags: ['', '', ''],
        SizedynamicTags: ['S', 'M', 'L'],
        inputVisible: false,
        inputVisible1: false,
        inputValue: '',
        inputValue1: '',
        centerDialogVisible: false,
        modelShop: '',
        shopList: [
          {
            value: 'New York',
            label: 'New York'
          },
          {
            value: 'London',
            label: 'London'
          },
          {
            value: 'Sydney',
            label: 'Sydney'
          }
        ],
        modelPrice: '',
        priceList: [
          {
            value: 'New York',
            label: 'New York'
          },
          {
            value: 'London',
            label: 'London'
          },
          {
            value: 'Sydney',
            label: 'Sydney'
          }
        ],
        valueSeach: '',
        containItems: []
      };
    },
    created() {
      var _this = this
      callApiWithToken('/api/listing/find_web_site_code_dist', {}, function (res) {
        _this.webSiteCodeList = res.data.content
        console.log(res)
      })
      callApiWithToken('/api/listing/query_listings', {'pageSize':10,'pageNum':1}, function (res) {
        _this.tableData = res.data.content.records
        console.log(res.data.content.records)
      })
    },
    mounted() {
      // this.initdata();
    },
    methods: {
      handleClose(tag) {
        this.ColordynamicTags.splice(this.ColordynamicTags.indexOf(tag), 1);
      },

      showInput() {
        this.inputVisible = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
      },

      handleInputConfirm() {
        let inputValue = this.inputValue;
        if (inputValue) {
          this.ColordynamicTags.push(inputValue);
        }
        this.inputVisible = false;
        this.inputValue = '';
      },
      addproduct() {
        var _this = this
        callApiWithToken('/api/listing/add_listing', this.listing, function (res) {
          if(res.data.success){
            _this.centerDialogVisible=false;
            alert("")
          }else{
            alert(res.data.errmsg)
          }
          console.log(res)
        })
        /*let param = {
            url:this.productLink,
          title:this.productName
      }
        console.log(param);
      this.$api.addProduct(param).then(res=>{

      })
     },
     initdata(){
         let param = {

         }
          this.$api.productList(param).then(res =>{

          })*/
      },
      grabproduct() {
        var _this = this
        callApiWithToken('/api/listing/grab_listing', {
          'listingUrl': this.listing.url,
          'webSiteCode': this.webSiteCode
        }, function (res) {
          _this.listing = res.data.content
          console.log(res)
        })
      },
      findwebstore(code) {
        this.listing.webSiteCode=code;
        /*var _this=this
        callApiWithToken('mbs/api/web_store/find_web_store_dist',{'webSiteCode':code},function (res) {
          _this.webStoreList=res.data.content
          console.log(res)
        })*/
      }
    }
  }
</script>
<style lang="scss">
  @import '../../../style/mixin';

  .productlist {
    background-color: #ffffff;
    padding-top: 15px;
    border: 1px solid #cccccc;
    border-top: none;
    min-height: 300px;
    overflow: hidden;
    .top {
      padding: 0 8px 0 16px;
      white-space: nowrap;
    }
    .contain {
      .containItem {
        width: 250px;
        float: left;
        font-size: 14px;
        margin: 10px 0 10px 16px;
        padding-bottom: 15px;
        box-sizing: border-box;
        border: 1px solid #e6e6e6;
        .productImgwrap {
          margin-bottom: 12px;
          width: 100%;
          height: 230px;
          position: relative;
          .el-checkbox {
            position: absolute;
            right: 10px;
            top: 10px;
          }
        }
        .productDetail {
          padding: 0 10px;
          margin-bottom: 12px;
          height: 28px;
          line-height: 14px;
          overflow: hidden;
          position: relative;
          &:after {
            content: '...';
            display: block;
            font-size: 14px;
            width: 42px;
            height: 13px;
            color: #000;
            background-color: #ffffff;
            position: absolute;
            right: 0;
            bottom: 0;
          }
        }
        .productPrice {
          padding: 0 10px;
          display: flex;
          justify-content: space-between;
          flex-wrap: nowrap;
          .price {
            font-size: 16px;
          }
          .wish {
            @include size(50px, 20px);
            background: url(./wish_logo.png) center no-repeat;
          }
        }
      }
    }
    .productstyle {
      overflow: hidden;
      p {
        white-space: nowrap;
      }
      .titledai {
        display: inline-block;
        width: 60px;
        margin-right: 10px;
      }
      .tagwrap {
        margin-top: 20px;
        .titledai {
          margin-right: 5px;
        }
        .el-tag {
          margin-right: 10px;
          margin-bottom: 5px;
        }
      }
      .dialogmainwrap {
        margin-top: 20px;
        display: flex;
        .leftwrap {
          flex: 1;
          p {
            margin-bottom: 20px;
            &:last-child {
              margin-bottom: 0;
            }
          }
        }
        .rightwrap {
          .Qimgwrap {
            width: 190px;
            height: 190px;
          }
        }
      }
    }
  }
</style>



