<template>
  <div class="trade">
    <div v-if="added">
      <div class="tradeAvatar">
        <img src="./111.png" width="100%" height="100%" alt="">
      </div>
      <div class="tradeName">
        <p class="detail">
        </p>
        <div class="priceWrap">
          <div class="left">
            <span>: {{listing.price}}</span>
            <span class="right">ASIN:{{listing.asin}}</span>
          </div>
          <div class="right">
            <el-checkbox v-model="listing.followSell"></el-checkbox>
            <el-checkbox v-model="listing.platformDelivery">FBA</el-checkbox>
          </div>
        </div>
        <div class="size">
          <span class="scTitle">Size : </span>
          <el-radio-group v-model="radioSize" size="small">
            <el-radio-button label="S"></el-radio-button>
            <el-radio-button label="M"></el-radio-button>
            <el-radio-button label="L"></el-radio-button>
            <el-radio-button label="XL"></el-radio-button>
          </el-radio-group>
        </div>
        <div class="color">
          <span class="scTitle">Color : </span>
          <el-radio-group v-model="radioColor" size="small">
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
          </el-radio-group>
        </div>
      </div>
    </div>
    <div v-else>
      <div class="tradeAvatar">
        <el-button @click="querylisting()"></el-button>
      </div>
    </div>
    <el-dialog
      title=""
      :visible.sync="centerDialogVisible"
      width="46%"
      center

      class="productstyle">
      <div class="listWrap">
        <el-table
          :data="tableData"
          @row-click="filllisting"
          style="width: 100%">
          <el-table-column
            prop="id"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="asin"
            label="ASIN"
            width="180">
          </el-table-column>
          <el-table-column
            prop="title"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            label="">
            <template slot-scope="scope">
              <el-button plain>  </el-button>
              <el-button type="primary"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import {callApiWithToken} from '@/data/callApi'

  export default {
    data() {
      return {
        centerDialogVisible: false,
        tableData: [],
        added: false,
        radioSize: '',
        radioColor: '',
        listing:{

        }
      }
    },
    methods: {
      querylisting() {
        var _this = this
        _this.centerDialogVisible = true;
        callApiWithToken('/api/listing/query_listings', {'pageSize': 10, 'pageNum': 1}, function (res) {
          _this.tableData = res.data.content.records
          console.log(res.data.content.records)
        })
      },
      filllisting(row) {
        this.centerDialogVisible = false;
        this.added=true;
        this.listing=row
        console.log(row)
      }
    }
  }
</script>
<style lang="scss">
  .trade {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    .tradeAvatar {
      width: 160px;
      height: 160px;
      box-sizing: border-box;
      border: 1px solid #cccccc;
      margin-right: 20px;
    }
    .tradeName {
      flex: 1;
      .detail {
        font-size: 14px;
        height: 32px;
        line-height: 16px;
        overflow: hidden;
        position: relative;
        padding-top: 5px;
        &:after {
          content: '...';
          display: block;
          font-size: 14px;
          width: 30px;
          height: 16px;
          color: #000;
          background-color: #ffffff;
          position: absolute;
          right: 0;
          bottom: 0;
        }
      }
      .priceWrap {
        overflow: hidden;
        padding-top: 10px;
        .left {
          width: 350px;
          span {
            &:first-child {
              color: #ee811e;
            }
          }
        }
      }
      .size {
        overflow: hidden;
        padding-top: 10px;
      }
      .color {
        overflow: hidden;
        padding-top: 10px;
      }
      .scTitle {
        display: inline-block;
        width: 45px;
        margin-right: 10px;
      }
    }
  }
</style>



